import { UserApi } from "../utils/api/user";
import { extraObj, getErrorMsg } from "../utils/support";
export default {
  name: "login",
  data() {
    return {
      form: {
        email: '',//"shanliang@qq.com",
        username:'',// "david-xxx",
        password:'',// "22222222",
      },
      errorList: [],
    };
  },
  computed: {
    isLogin() {
      return this.$route.name == "login";
    },
  },
  async asyncData({app}) {
    console.log(app.store.state.user,'xxxuser in cookie')
    return {};
  },
  beforeCreate() {
    console.log("beforeCreate");
  },
  created() {
    console.log("created");
  },
  methods: {
    onSubmit(e) {
      if (this.isLogin) {
        this.toLogin();
        return;
      }
      this.toRegister();
    },
    async toLogin() {
      const params = extraObj(this.form, ["email", "password"]);
      const { data } = await UserApi.login(params).catch((err) => {
        const error = getErrorMsg(err.data);
        if (error) {
          this.errorList = error;
        }
        return Promise.resolve({});
      });
      if (data) {
        this.errorList = [];
        this.$store.commit("user/setUser", data.user);
        this.$router.push('/')
      }
    },
    async toRegister() {
      const { data } = await UserApi.register(this.form).catch((err) => {
        const error = getErrorMsg(err.data);
        if (error) {
          this.errorList = error;
        }
        return Promise.resolve({});
      });
      if (data && data.user) {
        this.$router.replace("/login");
        this.errorList = [];
      }
    },
  },
};
